/* NAVBAR CSS */
nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(92, 92, 92);
    color: white;
    position: fixed;
    width: 100%;
    top: 0%;
    height: 80px;
}
 
ul {
    display: flex;
    align-items: center;
    gap: 52px;
    margin-right: 22px;
}
ul li{
    list-style: none;
}
.cart-icon{
    color: white;
    font-size: 20px;
}
ul p{
    font-size: 20px;
}
.logo{
    margin-left: 22px;
    font-size: 24px;
}

.top{
    margin-top: 122px;
}
.nav-items{
    text-decoration: none;
    color: white;
}
 .all-items{
    display: flex;
    align-items: center;
 }
.navIcons{
    display: none;
    font-size: 25px;
    cursor: pointer;
    color: white;
}
 
 
@media screen and (max-width:770px){
     ul {
        display: none;
     }
   .show-nav ul{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 11px;
        flex-direction: column;
        position: absolute;
        top: 0px;
        left: 0px;
        background-color: black;
        width: 100%;
        height:100vh;
        padding: 0px;
        margin: auto;
       
      
       
         
    }
     
    .navIcons{
        display: block;
        align-items: center;
        position: relative;
         left: -22px;
         
        
    }
    .hide-nav #crossIcon{
        display: none;
    }
    .show-nav #barIcon{
        display: none;
    }
}















/* main page css */
.main{
    display: flex;
    gap: 33px;
    justify-content: center;
    margin-top: 138px;
}
.main img{
    width: 500px;
}
.mainText{
    width: 500px;
}
.main-heading{
    font-size: 44px;
}
.main button{
    width: 140px;
    height: 37px;
    background-color: rgb(250, 58, 58);
    color: white;
    font-size: 20px;
    border: none;
    margin-top: 22px;
}
.main button:hover{
    background-color: rgb(252, 28, 28);
    cursor: pointer;

}

.main-card{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 54px;
}
.main-card .card{
    margin-top: 22px;
    width: 300px;
    height: 290px;
    margin-inline: 22px;
    background-color: #fff;
    box-shadow: 0px 3px 3px 3px rgb(228, 228, 228);
    border-radius: 5px;
    cursor: pointer;
}
.card img{
    width: 100%;
    height: 200px;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;

}
.card p{
    margin-left: 11px;
    margin-top: 0px;
}
.feature-card{
    text-decoration: none;
    color: black;
}
.card-head{
    font-weight: 600;
    font-size: 20px;
}
.location-head{
    text-align: center;
    font-size: 33px;
    margin-top: 85px;
}
.citys{
    display: flex;
    gap: 22px;
    flex-wrap: wrap;
    justify-content: center;
    
}
.citys .city-name{
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    width: 330px;
    height: 60px;
    padding: 1px;
    border-radius: 11px;
    background-color: #fff;
    box-shadow: 0px 3px 3px 3px rgb(224, 224, 224);
    cursor: pointer;
}
.citys .city-name:hover{
    box-shadow: 0px 3px 3px 3px rgb(192, 192, 192);
     
}
.city-name i{
    font-size: 11px;
}
.city-name p{
    font-size: 21px;
}
.cityNames{
    text-decoration: none;
    color: black;
}
@media screen and (max-width:1100px){
    .main{
        display: block;
        text-align: center;
    }
    .mainText{
        margin: auto;
    }
}
@media screen and (max-width:600px){
    .main img{
        width: 80%;
        
    }
    .mainText{
        margin: auto;
        width: 90%;
    }
    .main-heading{
        font-size: 30px;
    }
     
}
@media screen and (max-width:420px){
    .citys .city-name{
        width: 80%;
    }
    .location-head{
        font-size: 25px;
    }
    .main-card .card{
        width: 80%;
    }
}



















/* about css */
.about{
    display: flex;
    gap: 44px;
    justify-content: center;
    margin-top: 162px;
}
.aboutText{
    width: 500px;
}
.about .about-head{
    font-size: 44px;
}
.about button{
    width: 140px;
    height: 37px;
    background-color: rgb(245, 48, 48);
    border: none;
    font-size: 20px;
    color: white;
    cursor: pointer;
}
.aboutus{
    width: 800px;
    margin: auto;
    text-align: center;
    margin-top: 85px;
}
.aboutus-head{
    font-size: 40px;
}
@media screen and (max-width:1100px){
    .about{
        display: block;
        justify-content: center;
        text-align: center;
    }
    .about img{
        margin-top: 11px;
    }
    .aboutText{
        margin: auto;
    }
    .aboutus{
        width: 90%;
    }
}
@media screen and (max-width:650px){
    .about img{
        width: 80%;
    }
    .aboutText{
        margin: auto;
        width: 90%;
    }
     .aboutus-head{
        font-size: 30px;
    }
    .about .about-head{
        font-size: 30px;
    }
}









 



/* Food CSS */
.search-section{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    /* margin-top: 144px; */
}
.searchBar{
    display: flex;
    align-items: center;
}
.searchBar input{
    width: 600px;
    height: 38px;
    padding: 7px;
    font-size: 20px;
    border: none;
    outline: none;
    box-shadow: 0px 3px 3px 3px rgb(228, 227, 227);
    border-radius: 6px;
}
.logo{
    display: flex;
    text-decoration: none;
    color: black;
}
.contact-nav{
    text-decoration: none;
    font-size: 20px;
    color: rgb(109, 108, 108);
}
.contact-nav p:hover{
    color: black;
    border-bottom: 1px solid black;
}
.searchBar i{
    position: relative;
     margin-left: -32px;
    font-size: 22px;
    color: rgb(92, 91, 91);
}
.category{
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    align-items: center;
    margin-top: 22px;
   margin-left: 60px;
     
}
.category button{
    border: none;
    background-color: #fff;
    font-size: 22px;
    text-align: end;
    display: flex;
    gap: 11px;
}
.types{
     padding: 11px;
    border-bottom: 1px solid silver;
     
}
.category .category-type{
    display: flex;
    align-items: center;
    padding: 11px;
    border-radius: 11px;
    cursor: pointer;
    margin-left: 33px;
}
.category-type:hover {
    background-color: #f0f0f0;
}
.category-type i{
    background-color: rgb(252, 235, 190);
    padding: 15px;
    border-radius: 50%;
    font-size: 30px;
    color: rgb(29, 29, 29);
}
 
.category button:hover{
    color: rgb(250, 27, 64);
}
@media screen and (max-width:960px){
    .search-section{
        display: block;
        
    }
    .logo{
        display: flex;
        justify-content: center;
    }
    .search-section .searchBar{
       display: flex;
       justify-content: center;
    }
    .contact-nav p{
        display: none;
    }
}
@media screen and (max-width:700px){
    .searchBar input{
        width: 80%;
    }
    .category-type i{
        font-size: 22px;
    }
}

@media screen and (max-width:1040px){
    .category{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-left: 0px;
    }
    
     
}
@media screen and (max-width:450px){
    .category button{
        font-size: 17px;
         
    }
    .searchBar i{
        font-size: 17px;
    }
}


















/* Navigation CSS */
.navigation{
    display: flex;
    margin-left: 108px;
    margin-top: -11px;
}
.home-navigation{
    text-decoration: none;
    color: rgb(109, 108, 108);
}
.home-navigation:hover{
   color: black;
    
     
}
@media screen and (max-width:970px){
    .navigation{
       
        margin-top: 21px;
