/* CONTACT US */
.connect-text{
    margin-top: 111px;
    text-align: center;
    font-size: 33px;
}
.form{
    width: 340px;
    margin: auto;
    background-color: #ebebeb;
    text-align: center;
    padding: 11px;
}
.form input{
    width: 85%;
    border: none;
    padding: 11px;
    font-size: 18px;
    margin-top: 13px;
    border-radius: 4px;
}
textarea{
    width: 89%;
    margin-top: 13px;
    border: none;
    border-radius: 4px;

}
.form button{
    width: 110px;
    height: 30px;
    background-color: rgb(248, 42, 42);
    color: white;
    font-size: 20px;
    border: none;
    margin-top: 11px;
    border-radius: 3px;
    cursor: pointer;
}
.form button:hover{
    background-color: rgb(252, 69, 69);
    
}
@media screen and (max-width:450px){
    .form{
        width: 80%;
    }
}












/* select filters */
.filters{
    display: flex;
    gap: 22px;
    align-items: center;
    margin-top: 20px;
    margin-left: 99px;
}
.filters select{
    width: 180px;
    height: 35px;
    padding: 4px;
    border: 1px solid rgb(177, 177, 177);
    font-size: 16px;
    outline: none;
    cursor: pointer;
    border-radius: 4px;
}

.filter-btn{
    display: flex;
    gap: 22px;
}
.filter-btn .input-btn{
    width: 90px;
    height: 35px;
    background-color: #fff;
    border: 1px solid rgb(180, 179, 179);
    border-radius: 5px;
    font-size: 17px;
    cursor: pointer;
}
.filter-btn .input-btn :hover{
    background-color: #faf9f9;
    border: 1px solid rgb(0, 0, 0);

}
@media screen and (max-width:1040px){
    .filters{
        margin-left: 0px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding: 4px;
    }
}











/* CARD CSS */
.food-card{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 55px;
}
.cards {
    width: 300px;
    margin-inline: 20px;
    box-shadow: 0px 3px 3px 3px rgb(226, 226, 226);
    margin-top: 33px;
    cursor: pointer;
    border-radius: 5px;
}
.cards p{
    margin-left: 11px;
    margin-top: 4px;
    font-weight: 600;
    color: rgb(49, 49, 49);
}
.cards img{
    width: 100%;
    width: 100%;
    height: 200px;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}
.cards .rating{
    display: flex;
    gap: 103px;
    
     
}
.card-div{
    text-decoration: none;
    color: black;
}
.rating i{
    color: rgb(255, 166, 0);
    margin-inline: 2px;
}
 
@media screen and (max-width:370px){
    .cards{
        
        width: 90%;
    }
}













/* DETAIL PAGE */
.detail-page{
    display: flex;
    /* align-items: center; */
